nav {
  position: absolute;
  bottom: 5px;
  right: 5px;
  padding: 2px 5px;
  background-color: rgba(200, 200, 200, 0.3);
  border-radius: 3px;
}

p {
  font-family: sans-serif;
  font-size: 10px;
  color: #666;
  font-weight: 300;
  opacity: 0.5;
  text-transform: lowercase;

  a{
    color: #444;
    text-decoration: none;
    font-weight: 500;

    &:hover{
      text-decoration: underline;
    }
  }
}